<template>
  <b-container fluid>
    <b-container class="h-100">
      <b-row>
        <b-col md="12">
          <div class="main-player mx-auto w-100" id="siteplayer">
            <div class="d-flex justify-content-center py-5" id="siteplayer-loader" v-if="loader">
              <b-spinner label="Loading..." variant="primary" />
            </div>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </b-container>
</template>
<script>
  import DynamicJS from "@/js/core/dynamicjs";
  import Data from "@/js/core/data";

  export default {
    data() {
      return {
        loader: true,
      };
    },
    mounted: async function() {
      await DynamicJS.downloadCSS("https://infinity.500apps.com/css/siterecording/player-styles.css");
      await DynamicJS.loadWithPromise("https://infinity.500apps.com/js/siterecording/player.min.js", "player");
      this.playBackPlayer();
    },
    methods: {
      async playBackPlayer() {
        let region = new Data().getRegion();
        await window._sr_p.init(this.$route.params.session_id, "", region, window.domain);
        var interval = setInterval(() => {
          if (window.completed) {
            this.loader = false;
            clearInterval(interval);
          }
        }, 0);
      },
    },
  };
</script>
